@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');


* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #2c2b30;
    --color-bg-variant: #4f4f51;
    --color-primary: #f2c4ce;
    --color-primary-variant: #f58f7c;
    --color-white: #fefefe;
    --color-light: rgba(255,255,255,0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

}

 /* Add these theme-specific styles */
  
  body.light {
    background: url(../src/assets/light.jpg) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    --color-bg: #f0f0f0;
    --color-bg-variant: #e0e0e0;
    --color-primary: #0077be;
    --color-primary-variant: rgba(0, 119, 190, 0.4);
    --color-white: #333;
    --color-light: rgba(51, 51, 51, 0.6);
  }
  
  body.colorblind {
    background:black;
    --color-bg: #2c2c2c; /* Original dark gray */
    --color-bg-variant: #4f4f4f; /* Original medium gray */
    --color-primary: #b3b3b3; /* Grayscale equivalent of original primary */
    --color-primary-variant: #a0a0a0; /* Grayscale equivalent of original variant */
    --color-white: #ffffff; /* Pure white */
    --color-light: rgba(192, 192, 192, 0.6); /* Grayscale transparent light gray */
        
    
  }
  body.colorblind img{
    
    filter: grayscale(100%);
    
  }
  
  body.dark {
    --color-bg: #2c2b30;
    --color-bg-variant: #4f4f51;
    --color-primary: #f2c4ce;
    --color-primary-variant: #f58f7c;
    --color-white: #fefefe;
    --color-light: rgba(255,255,255,0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
  }
  

  /* Update your existing styles to use CSS variables */

  
  .container__glass {
    background: var(--color-bg-variant);
  }
  
  /* Update other color-related styles similarly */

html{
    scroll-behavior: smooth;
    cursor: default;
}

::-webkit-scrollbar{
    display:none;
}


body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg-variant);
    color: var(--color-white);
    line-height: 1.7;
    background: url(../src/assets/bgimg.jpg) no-repeat;
    background-size: cover;
    background-attachment: fixed;
   
}

/**/
.container{
    width:var(--container-width-lg);
    margin: 0 auto;
    
    
}
.container__glass{
    width:96%;
    margin: 0 auto;
    padding: 1.5rem 0;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.3);
    position: relative;
    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.07);
    backdrop-filter: blur(.3rem);
}

 





h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    
}
section > h2, section > h5, header > h2, header > h5 {
    text-align: center;
    color: var(--color-light);
}
section > h2, header > h2{
    color: var(--color-primary);
    margin-bottom: 2rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display:  inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transform: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}




/**/

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
    
}

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }
    

    section  >h2{
        margin-top: 2rem;
    }
    .container__glass{
        padding: 2rem 0 2rem 0;
    }
}




  
  