.about__container{
    display: grid;
    grid-template-columns: 30% 60%;
    gap: 10%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 0rem;

    background: linear-gradient(
        0deg,
        transparent,
        var(--color-primary)
    );

    display:grid;
    place-items: center;
}


.about__me-image{
    border-radius: 0rem;
    overflow: hidden;
    transform: translateX(2rem)translateY(1rem);
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(0);
   
}

.about__cards{
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 gap: 1.5rem;
}


.about__card{
    
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 0rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
    
}
.about__card__e{
    
    border: 1px solid transparent;
    border-radius: 0rem;
    padding: 0rem;
    text-align: center;
    transition: var(--transition);
    align-items: center;    
}


.extrac{
    margin: 3.43rem 0;
}

.extrac:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}






.extrac:hover span{
    display: none;
    
}
.extrac div{
    display: none;
}
.extrac:hover div{
    
    display:block;
    text-align: center;
    
}


.about__icon{
  color: var(--color-primary);  
  font-size: 1.4rem;
  margin-bottom: .6rem;
}
.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    text-align: justify;
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}




@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
       margin: 1rem 0 1.5rem; 
    }
    }

@media screen and (max-width: 600px) {
    
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;
       
    }
  

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }

    }    